"use client";
import { useState } from "react";

export default function Home() {
  const [loading, setLoading] = useState(false);
  const [assessment, setAssessment] = useState("phq-9");

  async function start() {
    setLoading(true);
    const res = await fetch("/api/sessions", {
      method: "POST",
      body: JSON.stringify({ assessmentSlug: assessment, userId: "demo" }),
    });
    const data = await res.json();
    window.location.href = `/sessions/${data.sessionId}`;
  }

  return (
    <main className="mx-auto max-w-xl p-6 space-y-6">
      <h1 className="text-2xl font-bold">Adaptive Assessment (Phase‑0)</h1>
      <label className="block">Choose assessment</label>
      <select
        value={assessment}
        onChange={(e) => setAssessment(e.target.value)}
        className="border p-2 rounded w-full"
      >
        <option value="phq-9">PHQ‑9</option>
        <option value="gad-7">GAD‑7</option>
      </select>
      <button
        disabled={loading}
        onClick={start}
        className="px-4 py-2 rounded bg-black text-white"
      >
        {loading ? "Starting…" : "Start Session"}
      </button>
    </main>
  );
}
